import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { useState } from "react";
const Contact = () => {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [subject, setSubject] = useState("");
  const [message, setMessage] = useState("");

  const nameHandler = (e) => {
    setName(e.target.value);
  };
  const emailHandler = (e) => {
    setEmail(e.target.value);
  };
  const subjectHandler = (e) => {
    setSubject(e.target.value);
  };
  const messageHandler = (e) => {
    setMessage(e.target.value);
  };
  const contactSubmit = (e) => {
    e.preventDefault();
    if (name.length > 0 && email.length > 0 && message.length > 0) {
      toast("Message sent!", {
        position: "top-center",
        autoClose: 3000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: false,
        progress: undefined,
      });
      setName("");
      setEmail("");
      setSubject("");
      setMessage("");
    } else {
      toast.dark("Fields* cannot be empty!", {
        position: "top-center",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: false,
        progress: undefined,
      });
    }
  };
  return (
    <div>
      <section className="contact" id="contact">
        <div className="container">
          <h2 className="contact-title">Contact Me</h2>
          <div className="contact-content">
            <form className="contact-form padd-15">
              <input
                onChange={nameHandler}
                value={name}
                name="name"
                type="text"
                className="form-control"
                placeholder="Name*"
              />
              <input
                onChange={emailHandler}
                value={email}
                name="email"
                type="text"
                className="form-control"
                placeholder="Email*"
              />
              <input
                onChange={subjectHandler}
                value={subject}
                name="subject"
                type="text"
                className="form-control"
                placeholder="Subject"
              />
              <textarea
                onChange={messageHandler}
                value={message}
                name="message"
                className="form-control"
                placeholder="Message*"
              ></textarea>
              <button onClick={contactSubmit} className="contact-btn-bg">
                Send Message
              </button>
            </form>
          </div>
        </div>
      </section>
      <ToastContainer
        position="top-center"
        autoClose={3000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable={false}
        pauseOnHover
      />
    </div>
  );
};

export default Contact;
