import React from "react";

const Skills = () => {
  return (
    <div>
      <section className="skills" id="skills">
        <div className="container">
          <h2 className="skills-title">Skills</h2>
          <div className="skills-content">
            <div className="card">
              <label>Assassin</label>
              <div className="progress" data-text="80%">
                <div className="inner" style={{width: "80%"}}></div>
              </div>
            </div>
            <div className="card">
              <label>Mage</label>
              <div className="progress" data-text="60%">
                <div className="inner" style={{width: "60%"}}></div>
              </div>
            </div>
            <div className="card">
              <label>Marksman</label>
              <div className="progress" data-text="90%">
                <div className="inner" style={{width: "90%"}}></div>
              </div>
            </div>
            <div className="card">
              <label>Fighter</label>
              <div className="progress" data-text="40%">
                <div className="inner" style={{width: "40%"}}></div>
              </div>
            </div>
            <div className="card">
              <label>Tank</label>
              <div className="progress" data-text="56%">
                <div className="inner" style={{width: "56%"}}></div>
              </div>
            </div>
            <div className="card">
              <label>Support</label>
              <div className="progress" data-text="78%">
                <div className="inner" style={{width: "78%"}}></div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
};

export default Skills;
